import {Component, OnDestroy, OnInit} from '@angular/core';
import {ProductsService} from "../../services/products.service";
import {Subject, Subscription} from "rxjs";

@Component({
  selector: 'app-admin-produkts',
  templateUrl: './admin-produkts.component.html',
  styleUrls: ['./admin-produkts.component.css']
})
export class AdminProduktsComponent implements OnDestroy, OnInit {

  products: any[]
  filteredProducts: any[]
  Subscrib: Subscription

  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject();

  constructor(private productServ: ProductsService) {
    this.Subscrib = this.productServ.getProducts().subscribe(products => {
        this.filteredProducts = this.products = products
        this.dtTrigger.next();
      }
    );
  }


  filter(queryString: string) {
    if (queryString) {
      this.filteredProducts =
        this.products.filter(
          p => p.payload.val()
            .title.toLowerCase().includes(queryString.toLowerCase()))
    } else {
      this.filteredProducts = this.products
    }
  }

  ngOnDestroy(): void {
    this.Subscrib.unsubscribe()
  }

  ngOnInit(): void {
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 10
    };
  }

}
