import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {ProduktComponent} from './produkt/produkt.component';
import {MeinComponent} from './mein/mein.component';
import {AdminOrdersComponent} from './admin/admin-orders/admin-orders.component';
import {AdminProduktsComponent} from './admin/admin-produkts/admin-produkts.component';
import {ShoppingCartComponent} from './shopping-cart/shopping-cart.component';
import {LoginComponent} from './login/login.component';
import {AuthGuard} from './services/auth-guard.service';
import {CheckOutComponent} from './check-out/check-out.component';
import {OrderSuccessComponent} from './order-success/order-success.component';
import {AdminManageUsersComponent} from './admin/admin-manage-users/admin-manage-users.component';
import {ProductsFormComponent} from "./admin/products-form/products-form.component";
import {AdminAuthGuardService} from './services/admin-auth-guard.service';
import {UserFormComponent} from './user/user-form/user-form.component';

const routes: Routes = [
  {path: '' , component : ProduktComponent },
  {path: 'products' , component : ProduktComponent },
  {path: 'check-out' , component : CheckOutComponent , canActivate: [AuthGuard]},
  {path: 'order-success' , component : OrderSuccessComponent , canActivate: [AuthGuard]},
  {path: 'orders' , component : MeinComponent, canActivate: [AuthGuard]},
  {path: 'admin/orders' , component : AdminOrdersComponent, canActivate: [AuthGuard , AdminAuthGuardService]},
  {path: 'admin/users' , component : AdminManageUsersComponent , canActivate: [AuthGuard , AdminAuthGuardService]},
  {path: 'admin/products' , component : AdminProduktsComponent , canActivate: [AuthGuard , AdminAuthGuardService]},
  {path: 'admin/products/new' , component : ProductsFormComponent , canActivate: [AuthGuard , AdminAuthGuardService]},
  {path: 'admin/products/:id' , component : ProductsFormComponent , canActivate: [AuthGuard , AdminAuthGuardService]},
  {path: 'shopping-cart' , component : ShoppingCartComponent},
  {path: 'login' , component : LoginComponent},
  {path: 'user-form' , component : UserFormComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})

export class AppRoutingModule {
}
