import {Component, OnDestroy, OnInit} from '@angular/core';
import {ProductsService} from "../services/products.service";
import {CategoriesService} from "../services/categories.service";
import {ActivatedRoute} from "@angular/router";
import {Observable, Subscription} from "rxjs";
import {ShoppingCardService} from "../services/shopping-card.service";
import {ShoppingCart} from "../model/shoppingCart";

@Component({
  selector: 'app-produkt',
  templateUrl: './produkt.component.html',
  styleUrls: ['./produkt.component.css']
})
export class ProduktComponent implements OnInit , OnDestroy{

  products : any[] = [];
  filteredProducts : any[];
  category= '';
  cart$: Observable<ShoppingCart>;
  subscriptio: Subscription;

  constructor(
    private route: ActivatedRoute,
    private productServ: ProductsService,
   private cartServ: ShoppingCardService
   ) {
    this.subscriptio =
      this.productServ.getProducts()
      .subscribe( products => {
        this.products = products
        this.route.queryParamMap.subscribe(parmas =>{
          this.category = parmas.get('category');
          this.filteredProducts = (this.category) ?
            this.products.filter(p => p.payload.val().category === this.category) : this.products;
        })
      })
  }

  async ngOnInit(): Promise<void> {
       this.cart$ = await this.cartServ.getCart();
  }
 ngOnDestroy(): void {
   this.subscriptio.unsubscribe()
 }

}
