import { BrowserModule } from '@angular/platform-browser';
import { NgModule} from '@angular/core';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { AppComponent } from './app.component';
import { NavBarComponent } from './nav-bar/nav-bar.component';
import { ProduktComponent } from './produkt/produkt.component';
import { MeinComponent } from './mein/mein.component';
import { AdminProduktsComponent } from './admin/admin-produkts/admin-produkts.component';
import { AdminOrdersComponent } from './admin/admin-orders/admin-orders.component';
import {AppRoutingModule} from './app-routing.module';
import { ShoppingCartComponent } from './shopping-cart/shopping-cart.component';
import { LoginComponent } from './login/login.component';
import { CssAnimateComponent } from './css-animate/css-animate.component';
import { TodosComponent } from './todos/todos.component';
import { AdrressComponent } from './adrress/adrress.component';
import {MatToolbarModule} from '@angular/material/toolbar';
import {MatButtonModule} from '@angular/material/button';
import {MatMenuModule} from '@angular/material/menu';
import {MatCardModule} from '@angular/material/card';
import {MatFormFieldModule} from '@angular/material/form-field';
import {MatDatepickerModule} from '@angular/material/datepicker';
import {MatNativeDateModule} from '@angular/material';
import {MatInputModule} from '@angular/material/input';
import {AngularFireDatabaseModule} from '@angular/fire/database';
import {AngularFireModule} from '@angular/fire';
import {environment} from '../environments/environment';
import {AngularFireAuthModule} from '@angular/fire/auth';
import {AuthService} from './services/auth.service';
import {AuthGuard} from './services/auth-guard.service';
import { HomeComponent } from './home/home.component';
import { CheckOutComponent } from './check-out/check-out.component';
import { OrderSuccessComponent } from './order-success/order-success.component';
import { AdminManageUsersComponent } from './admin/admin-manage-users/admin-manage-users.component';
import {UserService} from './services/user.service';
import { ProductsFormComponent } from './admin/products-form/products-form.component';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {CategoriesService} from "./services/categories.service";
import {CustomFormsModule} from "ng2-validation";
import {ProductsService} from "./services/products.service";
import {DataTablesModule} from "angular-datatables";
import { ProductCardComponent } from './product-card/product-card.component';
import { ProductFilterComponent } from './produkt/product-filter/product-filter.component';
import {ShoppingCardService} from "./services/shopping-card.service";
import {AdminAuthGuardService} from "./services/admin-auth-guard.service";
import { FooterComponent } from './footer/footer.component';

import { LOCALE_ID } from "@angular/core";
import {registerLocaleData} from "@angular/common";
import localeDE from '@angular/common/locales/de';
import { UserFormComponent } from './user/user-form/user-form.component';
registerLocaleData(localeDE);


@NgModule({
  declarations: [
    AppComponent,
    NavBarComponent,
    ProduktComponent,
    MeinComponent,
    AdminProduktsComponent,
    AdminOrdersComponent,
    ShoppingCartComponent,
    LoginComponent,
    CssAnimateComponent,
    TodosComponent,
    AdrressComponent,
    HomeComponent,
    CheckOutComponent,
    OrderSuccessComponent,
    AdminManageUsersComponent,
    ProductsFormComponent,
    ProductCardComponent,
    ProductFilterComponent,
    FooterComponent,
    UserFormComponent,

  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    NgbModule,
    BrowserAnimationsModule,
    MatToolbarModule,
    MatButtonModule,
    MatCardModule,
    MatFormFieldModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatInputModule,
    MatMenuModule,
    AngularFireDatabaseModule,
    AngularFireModule.initializeApp(environment.firebase),
    AngularFireAuthModule,
    FormsModule,
    CustomFormsModule,
    DataTablesModule,
    ReactiveFormsModule
  ],
exports : [
  MatToolbarModule,
  MatButtonModule,
  MatCardModule,
  MatFormFieldModule,
  MatDatepickerModule,
  MatNativeDateModule,
  MatInputModule
],
  providers: [
    {
      provide: LOCALE_ID, useValue: 'de'

    }  ,
    AuthService,
    AuthGuard,
    UserService,
    CategoriesService,
    ProductsService,
    ShoppingCardService,
    AdminAuthGuardService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
