import {Component, Input, OnInit} from '@angular/core';
import {expandCollapse} from '../animations';

@Component({
  selector: 'adrress',
  templateUrl: './adrress.component.html',
  styleUrls: ['./adrress.component.css'],
  animations: [
    expandCollapse
  ]
})
export class AdrressComponent implements OnInit {

  @Input('title') title: string;
  isExpanded: boolean;

  toggle(){
    this.isExpanded=!this.isExpanded;
  }

  constructor() { }

  ngOnInit() {
  }
}
