import {Component, OnInit} from '@angular/core';
import {CategoriesService} from "../../services/categories.service";
import {ProductsService} from "../../services/products.service";
import {ActivatedRoute, Router} from "@angular/router";
import 'rxjs/add/operator/take';

@Component({
  selector: 'app-products-form',
  templateUrl: './products-form.component.html',
  styleUrls: ['./products-form.component.css']
})
export class ProductsFormComponent implements OnInit {

   categories$
   product = {} ;
   id;

  constructor(
    private catgSrv: CategoriesService,
    private activRout: ActivatedRoute,
    private productServ: ProductsService,
    private router: Router
  ) {
    this.id = this.activRout.snapshot.paramMap.get('id');
    if (this.id) {
      this.categories$ = catgSrv.getCategories();
      this.productServ.getByID(this.id).take(1).subscribe(prod => {
        if (prod) {
          this.product = prod
        }
      })
    }

  }

  ngOnInit() {
    this.categories$ = this.catgSrv.getCategories();
  }

  save(product) {
    if (this.id) {
      this.productServ.updat(this.id, product)
    } else {
      this.productServ.create(product);
    }
    this.router.navigateByUrl('/admin/products');
  }


  delete() {
    if (confirm('Are you Sure you want to  delete the product ?')) {
      this.productServ.delete(this.id)
    }
    this.router.navigateByUrl('/admin/products');
  }

}
