import {ShoppingCartItem} from "./ShoppingCartItems";

export  class ShoppingCart {


  constructor(private items : ShoppingCartItem[] ){}


  get getTotalCount(){
    let counter = 0;
    for (let productId in this.items){
      counter += this.items[productId].quantity
    }
    return counter;
  }

  get productsIds(){
    return Object.keys(this.items)
  }

}
