import { Injectable } from '@angular/core';
import {AngularFireDatabase} from "@angular/fire/database";
import 'rxjs/add/operator/take'
import 'rxjs/add/operator/map'
import {ShoppingCart} from "../model/shoppingCart";
import {Observable} from "rxjs";

@Injectable({
  providedIn: 'root'
})
export class ShoppingCardService {

  constructor(
    private db: AngularFireDatabase
  ) { }


  private create(){
    return this.db.list('/Warenkorbs').push({
      dateCreated : new Date().getTime()
    })
  }



  private async  getOrCreateCartId(){
    let cartId = localStorage.getItem('cartId');
    if (cartId) return cartId;

    let result = await this.create()
    localStorage.setItem('cartId', result.key)
    return result.key
  }

  getItem(cartId: string, productId: string){
    return this.db.object('/Warenkorbs/'+ cartId+ '/items/'+ productId)
  }

 async addToCart(product){
      this.updateToCart(product, 1)
  }

  async removeFromCart(product){
      this.updateToCart(product, -1)
  }


  async updateToCart(product, quantityState){
    let cartId= await this.getOrCreateCartId()
    let item$ = this.getItem(cartId, product.key);
    item$.snapshotChanges().take(1).subscribe((item : any) => {
      if (item.payload.exists()){
        item$.update({quantity : item.payload.val().quantity + quantityState})
      } else {
        item$.update({
          product: {
            title: product.payload.val().title,
            price: product.payload.val().price,
            category: product.payload.val().category,
            imageUrl: product.payload.val().imageUrl
          },
          quantity: 1
        });
      }
    })
  }
  public async getCart() : Promise<Observable<ShoppingCart>>{
    let cartId= await this.getOrCreateCartId();
    return this.db.object('/Warenkorbs/'+ cartId).valueChanges().map(cart => new ShoppingCart((cart as any).items))
  }

}


